.testimonialSection{
  
 
  width: 100%;
   background-color: #fff;
   position: relative; /* Ensure proper layering */
  
}
.HeadingName{
    font-size: 4vw;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0px;
    line-height: 4vw;
}
.studentpara{
    font-size: 20px;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0px;
   
}
.studentName{
    font-size: 16px;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0px;
}
.studentimg{
    width: 20%;
}
@media (max-width: 480px) {
    .HeadingName {
        font-size: 7vw;
        font-family: Anton, serif;
        color: #000;
        text-align: left;
        margin-top: 0;
        line-height: 4vw;
    }
    .studentpara {
        font-size: 14px;
        font-family: Anton, serif;
        color: #000;
        text-align: left;
        margin-top: 0;
    }
    .studentimg {
        width: 18%;
    }
    .studentName {
        font-size: 14px;
        font-family: Anton, serif;
        color: #000;
        text-align: left;
        margin-top: 0;
    }
}