.carouselContainer {
    margin-top: 50px;
  }
  
  .carouselWrapper {
    overflow: hidden;
    position: relative;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .carousel {
    display: flex;
    width: 100%;
  }
  
  .carouselSlide {
    min-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .carouselButton {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 10px 15px;
    cursor: pointer;
    z-index: 10;
  }
  
  .prevButton {
    left: 10px;
  }
  
  .nextButton {
    right: 10px;
  }
  
  .indicators {
    margin-top: 15px;
  }
  
  .indicators button {
    border: none;
    cursor: pointer;
    font-size: 14px;
  }
  
  .activeIndicator {
    font-weight: bold;
    color: #0070f3;
  }
  
  .inactiveIndicator {
    color: #bbb;
  }
  

.UniversityStateSection{
    height: 100vh;
    position: relative;
  width: 100%;
   background-color: #145684;
   
  
}
.UniversityStateSectionImg{
    width: 80%;
}
.HeadingName{
    font-size: 4vw;
    font-family: Anton, serif;
    color: #fff;
    text-align: left;
    margin-top: 0px;
    line-height: 4vw;
}
.universityImgAlign{
    justify-content: center;
    position: relative;
    display: inline-flex;
