}
@media (max-width: 480px) {
    .HeadingName {
        font-size: 7vw;
        font-family: Anton, serif;
        color: #fff;
        text-align: left;
        margin-top: 0;
        line-height: 4vw;
    }
    .universityImgAlign {
        justify-content: center;
        position: relative;
        display: inline-flex ;
        margin-top: 3em;
        margin-bottom: 3em;
    }
    .UniversityStateSection {
        height: auto;
        position: relative;
        width: 100%;
        background-color: #145684;
    }
}