}
.LogoBox{
    position: absolute;
    top: 6%;
    /* width: 5%; */
    left: 3rem;
    display: flex;
    width: 95%;
}

  .marqueeContainer {
    position: absolute;
    top: 50%; /* Adjust as needed */
    left: 0;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    z-index: 0; /* Place behind the text */
}
  .marquee {
    display: inline-block;
    animation: scroll 10s linear infinite;
  }
  
  .marquee span {
    font-size: 10rem;
    margin-right: 2rem;
    color: rgba(22, 141, 255, 0.3);
  }
  
  @keyframes scroll {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
  }
  
  





  @media (max-width: 768px) {
    .BackgroundBlock h1 {
      font-size: 10vw;
      margin-top: 25%;
      line-height: 10vw;
    }
  
    .BackgroundBlock h4 {
      font-size: 3vw;
      margin-left: 3%;
    }
  
    .bulpImg {
