
.CampusSection{
  
    width: 100%;
    background-color: #fff;
    position: relative;
}
.HeadingName{
    font-size: 4vw;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0px;
    line-height: 4vw;
}
.CampusImg{
    border-radius: 30px;
    width: 100%;
}
.CampusParaBoxOuter{
    position: relative;  
}
.CampusParaBox p{
    position: absolute;
    bottom: 25%;
    font-size: 20px;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0px;

}
.CampusParaBox button{
    position: absolute;
    bottom: 0;
    font-size: 16px;
    font-family: Inter, serif !important;
    font-weight: 400 !important;
    color: #585858;
    text-align: left;
    margin-top: 0;
    border-radius: 9px;
    padding: 8px 24px;
    background-color: #fccc00;
}


.ScrollTextBox{
    top: 25%;
    left:0px;
    width: 100%;
    background: #FFFFFF;
    position: absolute;
   
    overflow: hidden;
  }
  

  .ScrollText {
    width:100%;
    font-size: 7rem;
    
    color:#168DFF;
    white-space: nowrap;
    text-transform: uppercase;
   
  }
  
  .marqueeStyle {
    display:inline-block;
    /* Apply animation to this element */
    animation: scrolling-left1 20s linear infinite;
  }
  
  .marqueeStyle2 {
    display:inline-block;
    /* Apply animation to this element */
    animation: scrolling-left2 20s linear infinite;
    animation-delay: 10s;
  }
  /* scrolling-left is continuous/repeatly text */
  @keyframes scrolling-left1 {
      0% {transform: translateX(100%);
          -webkit-transform: translateX(100%);}
        100% {transform: translateX(-100%);
            -webkit-transform: translateX(-100%);}
  }
  @keyframes scrolling-left2 {
      0% {transform: translateX(0%);
          -webkit-transform: translateX(0%);}
        100% {transform: translateX(-200%);
            -webkit-transform: translateX(-200%);}
  }
  
  @media (max-width: 480px) {
    .CampusParaBox p {
      position: relative;
      bottom: 0px;
      font-size: 14px;
      font-family: Anton, serif;
      color: #000;
      text-align: left;
      margin-top: 25px;
  }
  .HeadingName {
    font-size: 7vw;
    font-family: Anton, serif;
    color: #000;
    text-align: left;
    margin-top: 0;
    line-height: 4vw;
}
.CampusParaBox button {
  position: relative;
  bottom: 0;
  font-size: 11px;
  color: #585858;
  text-align: left;
  margin-top: 0;
  border-radius: 9px;
  padding: 6px 19px;
  background-color: #fccc00;
  font-family: Inter, serif !important;
  font-weight: 400 !important;
}
.ScrollText {
  width: 100%;
  font-size: 1rem;
  color: #168dff;
  display: none;
  white-space: nowrap;
  text-transform: uppercase;
}
  }
  






