.HeadingName{
    font-size: 4vw;
    font-family: Anton, serif;
    color: black;
    text-align: left;
    margin-top: 0px;
    line-height: 4vw;
}
.formStyle{
    margin-top: 5rem;
    width: 73%;
    margin-left: 11%;
}
.ImgStyle{
    width: 75%;
    margin-top: 11%;
    margin-left: 5%;
}
@media (max-width: 480px) {
    .HeadingName {
        font-size: 7vw;
        font-family: Anton, serif;
        color: #000;
        text-align: left;
        margin-top: 0;
        line-height: 4vw;
    }
    .ImgStyle {
        width: 86%;
        margin-top: 11%;
        margin-left: 5%;
    }
    .formStyle {
        margin-top: 1rem;
        width: 100%;
        margin-left: 0%;
    }
}